import React, { Component } from 'react';
import './Post.css';

class Post extends Component {
  constructor(props){
    super(props);
    this.name = props.name;
    this.qtdLikes = props.qtdLikes;
    this.post = props.post;
    this.lista = props.lista;
  }
  state = {
    cliques : 0,
    id : 0,
  }
   //Inicializar o estado dos cliques
  incrementarCliques = () =>{
    this.setState({
        cliques: this.state.cliques + 1,   
      });
    this.setState({
      id : this.state.id + 1,
    })
    
    alert('VocÃª recebeu um like!')
  }
  render() { 
    return (
      <div className = 'Post'>
        <div className = 'back-post'>
          <p> {this.name}</p>
          <p> {this.post}</p>
          <div>
            <button onClick = {this.incrementarCliques}>
                      Likes{this.state.cliques} 
            </button>
          </div>
        </div>
      </div>
      );
  }
}
 
export default Post;



